.categories-section{
        width: 100%;
}
.grid{
        max-width: 800px;
        display: grid;
        gap: var(--gap-050);
        grid-template-columns: 100%;
        grid-template-rows: repeat(4, 1fr);
        margin: 0 auto;
}

.grid .option{
        border-radius: 10px;
        padding: 0.5rem 0;
        font-size: 1rem;

        display: flex;
        justify-content: center;
        align-items: center;
}

.optionA{
        background-color: var(--clr-1);
}

.optionB{
        background-color: var(--clr-2);

}

.optionC{
        background-color: var(--clr-3);

}

.optionD{
        background-color: var(--clr-4);

}

@media (min-width: 700px) {
        .option-title p{
                font-size: 1.7rem;
        }
        .grid{
                grid-template-columns: repeat(3, 1fr);
                grid-template-rows: repeat(2, 50%);
                grid-template-areas: 
                "AA AA BB"
                "CC DD DD";
                margin: 0 auto 3rem auto;
        }

        .option{
                height: 200px;
        }
             
        .optionA{ grid-area: AA; }
        .optionB{ grid-area: BB; }
        .optionC{ grid-area: CC; }
        .optionD{ grid-area: DD; }
}

@media (min-width: 1000px) {
        .grid{
                margin: 0;
        }
}
