.content-section{
        width: 100%;
        height: 50%;

        display: flex;
        flex-direction: column;
        align-items: center;
        gap: 1rem;
        /* padding: 2rem 0; */  
}

.content-section .content-title{
        font-size: 1.2rem;
        color:  var(--clr-5);
}

.content-section .benefit{
        font-size: 1.1rem;
        color:  var(--clr-5);
}

.content-section .content-cta{
        width: 50%;
        height: 2rem;

        background-color: var(--clr-2);
        border-radius: 10px;
        border: none;

        font-size: 1.1rem;
}
.content-section .content-cta:hover{
        background-color: var(--clr-1);
}

@media (min-width: 700px) {
        .content-section .content-cta{
                width: 90%;
                height: 4rem;
        
                background-color: var(--clr-2);
                border-radius: 10px;
                border: none;
        
                font-size: 1.4rem;
        }
        .content-section .content-title{
                font-size: 2rem;
        }

        .content-section .benefit{
                font-size: 1.5rem;
        }
}


@media (min-width: 1000px) {
        .content-section .content-title{
                font-size: 2rem;
        }

        .content-section .benefits{
                margin-bottom: 6rem;
        }
}