*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-size: 18px;
}
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

:root{
  --clr-1: #F4B400;
  --clr-2: #4285F4;
  --clr-3: #DB4437;
  --clr-4: #0F9D58;
  --clr-5: rgb(1, 1, 22);
  --clr-6: #fffafa;

  --gap-100: 1rem;
  --gap-050: 0.5rem;
  --pad: 0.5rem;

  --header-y: 3rem;
  --max-width: 1200px;
}

a{
  text-decoration: none;
  color: var(--clr-5);
}

.container{
  width: 100%;
  max-width: var(--max-width);
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 var(--pad);
  margin: 0 auto;
}




