.header, .header nav, .header nav ul, .login{
        display: flex;
}

.header{
        width : 100vw;
        height: var(--header-y);

        justify-content: space-between;
        align-items: center;
        border-bottom: 2px solid var(--clr-2);
}

.header .navigator{
        position: relative;
        list-style: none;
        gap: 1rem;
        display: none;
        align-items: center;
}

.navigator .login{
        padding : 0.3rem var(--gap-100);
        background-color: var(--clr-2);
        font-weight: bold;
        border-radius: 10px;
        display: flex;
        justify-content: center;
        align-items: start;
}

.toggle{
        width: 30px;
        height: 30px;
}

@media (min-width: 700px) {
        .container{
                padding: calc(var(--pad) * 5);
        }
        .toggle{
                display: none;
        }

        .header .navigator{
                display: flex;
        }
}

@media (min-width: 700px) {
        .toggle{
                display: none;
        }

        .header .navigator{
                display: flex;
        }
}


