.home .container{
        flex-direction: column;
        gap: var(--gap-100);
        padding-bottom: var(--pad);
}
.home{
        display: flex;
        align-items: center;
}

@media (min-width: 700px) {
        .home .container{
                flex-direction: column;
        }
}

@media (min-width: 1000px) {
        .home .container{
                flex-direction: row;
                justify-content: center;
                padding: 0;
        }
        .content-section, .categories-section{
                margin: 0;
                padding: 0;
        }
}